import React from "react";
import { BodyWrapper, Logo, GiftFooter, Message, Desc, BasicButton, Form } from "..";

function GiftBodyInfoGift(props) {
  return (
    <BodyWrapper>
    <Logo />
      <Message contents="   !" />
      <Desc contents="   " />
      <BasicButton do={() => localStorage.setItem("gift_gender", "")} text="" />
      {/* <BasicButton do={() => localStorage.setItem("gift_gender", "")} text="" /> */}
      <Form label="" field="gift_gender" type="button" />
      <Form label="" field="gift_age" type="age" />
      <Form label="" field="gift_price" type="price" />
      <GiftFooter before={props.before} next={props.next} />
    </BodyWrapper>
  );
}

export default GiftBodyInfoGift;



